import { ProcessStepElement, ProcessStepElementBase } from '@jumoo/processing';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { uSyncSnapshots } from '../../constants';

@customElement(uSyncSnapshots.steps.reportResults)
export default class uSyncSnapshotsReportResultsElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	connectedCallback(): void {
		super.connectedCallback();

		const changes = this.results?.results['changes'] as number;
		if (changes == 0) {
			console.log('no changes, skipping');
			this.dispatchEvent(
				new CustomEvent('pipeline-valid', {
					bubbles: true,
					composed: true,
					detail: {
						valid: false,
					},
				}),
			);
		}
	}

	render() {
		var actions = this.results?.results['actions'] as any[];

		return html`<uui-box headline=${this.localize.term('uSyncSnapshots_report')}>
			<usync-results .results=${actions}></usync-results
		></uui-box>`;
	}
}
